import { requireDetailer } from '@/lib/auth';
import { createClient } from '@/lib/supabase/server';
import EarningsPageClient from './EarningsPageClient';

export default async function EarningsPage() {
  const profile = await requireDetailer();
  const supabase = await createClient();

  // Get detailer record
  const { data: detailerData } = await supabase.rpc('get_detailer_by_profile', {
    p_profile_id: null,
  });

  let earningsData: any[] = [];
  let totalEarnings = 0;
  let pendingPayouts = 0;

  if (detailerData?.id) {
    // Get all completed bookings
    const { data: bookings } = await supabase
      .from('bookings')
      .select(
        `
        id,
        receipt_id,
        status,
        total_amount,
        service_price,
        addons_total,
        tax_amount,
        completed_at,
        scheduled_date,
        service:service_id (name),
        user:user_id (full_name),
        car:car_id (make, model, year)
      `
      )
      .eq('detailer_id', detailerData.id)
      .eq('status', 'completed')
      .order('completed_at', { ascending: false });

    earningsData = bookings || [];
    totalEarnings = earningsData.reduce((sum, b) => sum + (b.total_amount || b.service_price || 0), 0);
    
    // Pending payouts are completed bookings that haven't been paid out yet
    // For now, all completed bookings are considered pending until Stripe Connect is implemented
    pendingPayouts = totalEarnings * 0.9; // Assume 10% platform fee
  }

  return (
    <div className="min-h-screen bg-[#050B12] text-white">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-white mb-2">Earnings</h1>
          <p className="text-[#C6CFD9]">View your earnings and payout history</p>
        </div>

        <EarningsPageClient 
          earningsData={earningsData}
          totalEarnings={totalEarnings}
          pendingPayouts={pendingPayouts}
        />
      </div>
    </div>
  );
}

